import React, { Component, FormGroup } from "react";
import Img from "gatsby-image";

class Projects extends Component {
  constructor(props) {
    super(props);

  this.state = {
      selectedType: "front-end"
   };

  this.onSelectChange = this.onSelectChange.bind(this);
  }
 
  onSelectChange(e) {
    this.setState({ selectedType: e.target.value });
 }

render() {
    const projectImgs = this.props.projectImgs;
    const { selectedType } = this.state;
    const projectList = [{
        type: "front-end",
        img: "tree.png" ,
        url: "#",
        name: "This is a project",
        description: "This is a description"

    },
    {
        type: "front-end",
        img: "tree.png" ,
        url: "#",
        name: "This is a project",
        description: "This is a description"

    }]
    console.log("project Images", projectImgs)
    return (
      <section id="projects" className="section projects">
        <h2 className="text-center">PROJECTS</h2>
        <div className="section-content">
          <div className="subheader">
          </div>
          <div className="project-list">
            {projectList.map(project => {
              const isSelectedType = selectedType === project.type;
              const singleCardClass = "single-card";
              const image = projectImgs[0];
              const imageSizes = image.node.childImageSharp.sizes;
              return (
                <a
                  href={project.url}
                  key={project.url}
                  className={singleCardClass}
                  target="_blank"
                >
                  <div className="card-img">
                    <Img
                      title={project.name}
                      alt="Screenshot of Project"
                      sizes={imageSizes}
                      className="card-img_src center-block"
                    />
                  </div>
                  <div className="blue-divider" />
                  <div className="card-info">
                    <h4 className="card-name">{project.name}</h4>
                    <p>{project.description}</p>
                  </div>
                </a>
              );
            })}
          </div>
        </div>
      </section>
    );
  }
}

export default Projects;